.even\:flex-row:nth-child(even) {
	flex-direction: row;
}

.hover\:scale-110:hover {
	--tw-scale-x: 1.1;
	--tw-scale-y: 1.1;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}

.hover\:text-gray-700:hover {
	--tw-text-opacity: 1;
	color: rgba(55, 65, 81, var(--tw-text-opacity));
}

.active\:scale-95:active {
	--tw-scale-x: .95;
	--tw-scale-y: .95;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}

@media (min-width: 640px) {

	.sm\:ml-0 {
		margin-left: 0px;
	}

	.sm\:w-\[80\%\] {
		width: 80%;
	}

	.sm\:grid-cols-2 {
		grid-template-columns: repeat(2, minmax(0, 1fr));
	}

	.sm\:justify-start {
		justify-content: flex-start;
	}

	.sm\:space-x-8 > :not([hidden]) ~ :not([hidden]) {
		--tw-space-x-reverse: 0;
		margin-right: calc(2rem * var(--tw-space-x-reverse));
		margin-left: calc(2rem * calc(1 - var(--tw-space-x-reverse)));
	}

	.sm\:text-left {
		text-align: left;
	}
}

@media (min-width: 768px) {

	.md\:mx-6 {
		margin-left: 1.5rem;
		margin-right: 1.5rem;
	}

	.md\:mt-0 {
		margin-top: 0px;
	}

	.md\:mt-8 {
		margin-top: 2rem;
	}

	.md\:flex {
		display: flex;
	}

	.md\:w-\[40\%\] {
		width: 40%;
	}

	.md\:w-2\/4 {
		width: 50%;
	}

	.md\:min-w-full {
		min-width: 100%;
	}

	.md\:max-w-\[50\%\] {
		max-width: 50%;
	}

	.md\:max-w-\[40\%\] {
		max-width: 40%;
	}

	.md\:max-w-\[55\%\] {
		max-width: 55%;
	}

	.md\:grid-cols-3 {
		grid-template-columns: repeat(3, minmax(0, 1fr));
	}

	.md\:flex-row {
		flex-direction: row;
	}

	.md\:gap-4 {
		gap: 1rem;
	}

	.md\:text-left {
		text-align: left;
	}
}

@media (min-width: 1024px) {

	.lg\:col-span-2 {
		grid-column: span 2 / span 2;
	}

	.lg\:mx-10 {
		margin-left: 2.5rem;
		margin-right: 2.5rem;
	}

	.lg\:mt-12 {
		margin-top: 3rem;
	}

	.lg\:flex {
		display: flex;
	}

	.lg\:hidden {
		display: none;
	}

	.lg\:grid-cols-5 {
		grid-template-columns: repeat(5, minmax(0, 1fr));
	}

	.lg\:space-x-8 > :not([hidden]) ~ :not([hidden]) {
		--tw-space-x-reverse: 0;
		margin-right: calc(2rem * var(--tw-space-x-reverse));
		margin-left: calc(2rem * calc(1 - var(--tw-space-x-reverse)));
	}
}

